import React, { Component } from 'react'
import { List } from 'antd';
import { Link } from "react-router-dom";
import "./index.less";
const listData = [];
for (let i = 0; i < 1; i++) {
    listData.push({
        href: '/gojs',
        className: "gojs",
        title: `GoJS 入门教程`,
        description:
            'GoJS 简介',
        content:
            'GoJS是一个用于构建交互式图表和图形的JavaScript和TypeScript库。GoJS允许你建立各种各样的图表和图形，从简单的流程图和组织图到非常具体的工业图，SCADA和BPMN图，医学图，如基因图和疫情建模图等。GoJS使用可定制的模板和布局使构建复杂节点、链接和组的JavaScript关系图变得更容易。',
    });
}

export default class index extends Component {
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                dataSource={listData}
                renderItem={item => (
                    <Link to={item.href}>
                        <List.Item
                            className={item.className}
                            key={item.title}
                            extra={
                                <div className="box">
                                    <img src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png" alt={item.title} />
                                    <div className="box-content">
                                        <h3 className="title">点击进入教程</h3>
                                    </div>
                                </div >

                            }
                        >
                            <List.Item.Meta
                                title={item.title}
                                description={item.description}
                            />
                            <p style={{ textIndent: "2em", color: "rgba(0, 0, 0, 0.65)" }}>{item.content}</p>
                        </List.Item>
                    </ Link>
                )}
            />
        )
    }
}
